<template>
    <div>

        <!--  -->
        <section>

        <div class="curtain"></div>

        <div class="rightCurtain1"></div>
        <div class="rightCurtain2"></div>

        </section>
        <!--  -->
       
       <section>

           <div class="row marginpaddingFix">
               <div class="col marginpaddingFix">
                    <div class="aboutTitle d-flex justify-content-center align-items-center">
                        <div class="preFadein">
                        <h2 class="h1 text-primary text-center">Who we are</h2>
                        <img src="static/img/aboutus.svg" alt="">
                        </div>
                    </div>
               </div>
           </div>

       </section>
       <!--  -->


       <section>

           <div class="aboutContent aboutContainer">

           <div class="row timeLine aboutContentItem flex-row-reverse preFadeinLeft">
               
               <div class="col-lg-6">
                    <h3 class="h2 text-primary">
                        2015/02 - 
                    </h3>
                    <p class="h6 lh-global text-secondary">
                        <span class="text-primary"></span><span class="text-primary"></span>
                    </p>
               </div>
               <div class="col-lg-6 text-center  d-flex justify-content-center align-items-center">
                       <img src="static/img/about1.svg" alt="">
               </div>

           </div>

            <div class="row timeLine aboutContentItem preFadeinRight">
               
               <div class="col-lg-6">
                    <h3 class="h2 text-primary text-right">
                        2019/01 - 
                    </h3>
                    <p class="h6 text-secondary lh-global">
                        2019:2/3 ? <span class="text-primary"></span>
                    </p>
               </div>
               <div class="col-lg-6">
                   <div class="text-center  d-flex justify-content-center align-items-center">
                       <img src="static/img/about2.svg" alt="">
                   </div>
               </div>

           </div>

            <div class="row timeLine aboutContentItem flex-row-reverse preFadeinLeft">
               
               <div class="col-lg-6">
                    <h3 class="h2 text-primary">
                        2019/04 - 
                    </h3>
                    <p class="h6 lh-global text-secondary">
                        <span class="text-primary"></span>
                    </p>
               </div>
               <div class="col-lg-6">
                   <div class="text-center d-flex justify-content-center align-items-center">
                       <img src="static/img/about3.svg" alt="">
                   </div>
               </div>

           </div>

           <div class="row timeLine aboutContentItem preFadeinRight">
               
               <div class="col-lg-6">
                    <h3 class="h2 text-primary text-right">
                        2020/10 - 
                    </h3>
                    <p class="h6 text-secondary lh-global">
                        
                    </p>
               </div>
               <div class="col-lg-6">
                   <div class="text-center d-flex justify-content-center align-items-center">
                       <img src="static/img/about4.svg" alt="">
                   </div>
               </div>

           </div>

           <div class="row timeLineExtend">
               <div class="col">
                   <img src="static/img/about5.svg" alt="">
               </div>
           </div>

           </div>


       </section>

    </div>
</template>

<script>

import $ from 'jquery';

export default {

    methods : {
    openPage(){
        setTimeout(() => {
            document.body.scrollTop = 0;
            document.documentElement.scrollTop = 0;

            setTimeout(() => {
                $('.preFadein').addClass('fadeIn');
            }, 500);

            $('.curtain').addClass('curtainShow');
        }, 5);
    },
        addAnimate(){
        const scrollPos = $(window).scrollTop();
        const windowHeight = $(window).height();

        $('.preFadein').each(function(){
            const itemPos = $(this).offset().top;

            if((scrollPos + windowHeight) > (itemPos + 100)){
                $(this).addClass('fadeIn');
            }
        })

        $('.preFadeinRight').each(function(){
            const itemPos = $(this).offset().top;

            if((scrollPos + windowHeight) > (itemPos + 100)){
                $(this).addClass('fadeIn');
            }
        })

        $('.preFadeinLeft').each(function(){
            const itemPos = $(this).offset().top;

            if((scrollPos + windowHeight) > (itemPos + 100)){
                $(this).addClass('fadeIn');
            }
        })

        $('.preShake').each(function(){
            const itemPos = $(this).offset().top;

            if((scrollPos + windowHeight) > (itemPos + 150)){
                $(this).addClass('shake');
            }
        })
    },
    },
    created(){
        this.openPage();
        window.addEventListener('scroll' , this.addAnimate);
    }
}
</script>


