<template>
    <div>

        <!--  -->
        <section>

        <div class="curtain"></div>

        <div class="rightCurtain1"></div>
        <div class="rightCurtain2"></div>

        </section>
        <!--  -->

        <!-- banner -->
        <section>
            <div class="container">

                <div class="product-banner-border">
                <div class="products-banner">

                    <vue-typer
                    :text='["",""]'
                    :repeat='Infinity'
                    :shuffle='false'
                    initial-action='typing'
                    :pre-type-delay='300'
                    :type-delay='200'
                    :pre-erase-delay='3000'
                    :erase-delay='150'
                    erase-style='backspace'
                    :erase-on-complete='false'
                    caret-animation='blink'
                    class="h2"
                    ></vue-typer>

                </div>

                </div>

            </div>
        </section>
        <!--  -->

        <!--  -->
        <section>

            <div class="container-fluid products-area">

            <div class="row">
                <div class="col-md-4 my-3 d-flex justify-content-center" v-for="item in products" :key="item.id">
                    <div class="products-item h-100 p-4">
                        <div class="d-flex" @click.prevent="openProductModal(item)">
                        <img :src="item.imageUrl" alt="" width="40%" height="160px">
                        <div>
                            <h2 class="h6 text-primary mt-3"> {{ item.title }} </h2>
                            <span> {{ item.description }} </span>
                        </div>
                        </div>
                        <div class="d-flex justify-content-between">
                            <h5> {{ item.origin_price | dollarSign }} </h5>
                            <small class="text-danger font-weight-bold"> {{ item.price | dollarSign }} </small>
                        </div>
                        <button class="btn btn-primary text-white w-100 mt-3" @click.prevent="addToCart(item.id)">
                            <i class="fas fa-spinner fa-pulse" v-if="addToCartLoading"></i>
                            </button>
                    </div>
                </div>
            </div>

            </div>

        </section>
        <!--  -->

        <!-- modal -->
        <div class="modal fade" id="productModal" data-backdrop="static" data-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-xl">
            <div class="modal-content product-item-modal">
            <div class="modal-body">
                    <button type="button" class="close product-item-close" data-dismiss="modal" aria-label="Close" @click="productIsLoading = false">
                            <span aria-hidden="true">&times;</span>
                    </button>
                <div class="row">
                    <div class="col-lg-6 d-flex justify-content-center">
                        <img :src="tempProduct.imageUrl" alt="" width="70%" height="80%" class="preFadeinRight" :class="{'fadeIn' : productIsLoading}">
                    </div>
                    <div class="col-lg-6 mt-3 px-5 product-content">
                        <h2 class="h3 text-primary mb-2 preFadein" :class="{'fadeIn' : productIsLoading}"> {{ tempProduct.title }} </h2>
                        <p class="h6 text-secondary preFadein my-3" :class="{'fadeIn' : productIsLoading}"> {{ tempProduct.description }} </p>
                        <p class="h6 text-secondary mt-5 lh-global preFadein" :class="{'fadeIn' : productIsLoading}"> {{ tempProduct.content }} </p>
                    </div>
                </div>

                <div class="row">
                    <div class="col-lg-6">

                    </div>
                    <div class="col-lg-6 d-flex justify-content-end">
                        <select v-model="tempProduct.num" class="w-50">
                            <option :value="item" v-for="item in 5" :key="item.id"> {{ item }} {{ tempProduct.unit }} </option>
                        </select>
                        <button type="button" class="btn btn-primary text-white w-50 ml-3" @click="addToCart(tempProduct.id , tempProduct.num)">
                            <i class="fas fa-spinner fa-pulse" v-if="addToCartLoading"></i>
                            </button>
                    </div>
                </div>

            </div>
            </div>
        </div>
        </div>
        <!--  -->

    </div>
</template>

<script>

import $ from 'jquery';

export default {
    data(){
        return{
            products : [],
            tempProduct : {},
            productIsLoading : false,
            addToCartLoading : false
        }
    },
    methods : {
        getProducts(){
            const self = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/products/all`;

            self.$http.get(api).then((res) => {
                if(res.data.success){

                    document.body.scrollTop = 0;
                    document.documentElement.scrollTop = 0;

                    $('.curtain').addClass('curtainShow');

                    self.products = res.data.products;
                }else{

                    document.body.scrollTop = 0;
                    document.documentElement.scrollTop = 0;

                    $('.curtain').addClass('curtainShow');
                }

            })
        },
        openProductModal(item){
            const self = this;

            self.tempProduct = item;
            self.tempProduct.num = 1;
            
            
            $('#productModal').modal('show');

            setTimeout(() => {
                self.productIsLoading = true;
            }, 200);

        },
        addToCart(id , qty = 1){
            const self = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/cart`;

            let cart = {
                product_id : id,
                qty
            };

            self.addToCartLoading = true;
            self.$http.post(api , {data : cart}).then((res) => {
                if(res.data.success){
                    $('#productModal').modal('hide');
                    console.log(res.data);
                    console.log(cart);
                    self.addToCartLoading = false;
                }
            })
        }
    },
    created(){
        this.getProducts();
    }
}
</script>


